import React, { useEffect, useRef, useState } from 'react';
import { useDispatchCart, useCart } from './ContextReducer';
import { useNavigate } from 'react-router-dom';

export default function Card(props) {
  let dispatch = useDispatchCart();
  let data = useCart();
  const priceRef = useRef();
  const { options = {} } = props;

  let priceOptions = Object.keys(options);
  const [qty, setQty] = useState(1);
  const [size, setSize] = useState("");

  const handleAddToCart = async () => {
    let existingItem = data.find(item => item.id === props.foodItem._id && item.size === size);

    if (existingItem) {
      await dispatch({
        type: "UPDATE",
        id: props.foodItem._id,
        price: finalPrice,
        qty: qty
      });
    } else {
      await dispatch({
        type: "ADD",
        id: props.foodItem._id,
        name: props.foodItem.name,
        price: finalPrice,
        qty: qty,
        size: size
      });
    }
  };

  let finalPrice = qty * parseInt(options[size] || 0, 10); // Default to 0 if size is invalid

  useEffect(() => {
    setSize(priceRef.current.value);
  }, []);

  return (
    <div className="card mt-3" style={{ width: "18rem", maxHeight: "360px", margin: "0.5rem" }}>
      <img
        src={props.foodItem.img}
        className="card-img-top"
        alt={props.foodItem.name}
        style={{ height: "120px", objectFit: "fill" }}
      />
      <div className="card-body">
        <h5 className="card-title">{props.foodItem.name}</h5>
        <div className='container w-100'>
          <select className='m-2 h-100 bg-success rounded' onChange={(e) => setQty(e.target.value)}>
            {Array.from(Array(6), (e, i) => (
              <option key={i + 1} value={i + 1}> {i + 1} </option>
            ))}
          </select>
          <select className='m-2 h-100 bg-success rounded' ref={priceRef} onChange={(e) => setSize(e.target.value)}>
            {priceOptions.map((data) => (
              <option key={data} value={data}>{data}</option>
            ))}
          </select>
          <div className='d-inline h-100 fs-5'>
            Rs.{finalPrice}/-
          </div>
          <hr />
          <button className={'btn btn-success justify-center ms-2'} onClick={handleAddToCart}>Add To Cart</button>
        </div>
      </div>
    </div>
  );
}
